import React, { Fragment, useState } from 'react'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addEducation } from '../../actions/profile'
import { Link } from 'react-router-dom'

const AddEducation = ({ addEducation, history }) => {
  const [formData, setFormData] = useState({
    school: '',
    degree: '',
    fieldofstudy: '',
    from: '',
    to: '',
    current: false,
    description: '',
  })

  const [toDateDisabled, toggleDisabled] = useState(false)

  const { school, degree, fieldofstudy, from, to, current, description } =
    formData

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value })

  return (
    <Fragment>
      <section className='container'>
        <h1 className='large text-theme text-center mg-1'>
          {' '}
          <i className='fas fa-university' /> Add Your Education
        </h1>
        <div className='form-box'>
          <p className='lead ff-Work-Sans'>
            Add any school or bootcamp that you have attended
          </p>
          <small>* = required field</small>
          <form
            className='form'
            onSubmit={(e) => {
              e.preventDefault()
              addEducation(formData, history)
            }}
          >
            <div className='form-group'>
              <input
                type='text'
                placeholder='* School or Bootcamp'
                name='school'
                value={school}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div className='form-group'>
              <input
                type='text'
                placeholder='* Degree or Certificate'
                name='degree'
                value={degree}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div className='form-group'>
              <input
                type='text'
                placeholder='Field of Study'
                name='fieldofstudy'
                value={fieldofstudy}
                onChange={(e) => onChange(e)}
              />
            </div>
            <div className='form-group'>
              <h4>From Date</h4>
              <input
                type='date'
                name='from'
                value={from}
                onChange={(e) => onChange(e)}
              />
            </div>
            <div className='form-group'>
              <p>
                <input
                  type='checkbox'
                  name='current'
                  checked={current}
                  value={current}
                  onChange={(e) => {
                    setFormData({ ...formData, current: !current })
                    toggleDisabled(!toDateDisabled)
                  }}
                />{' '}
                Current School
              </p>
            </div>
            <div className='form-group'>
              <h4>To Date</h4>
              <input
                type='date'
                name='to'
                value={to}
                onChange={(e) => onChange(e)}
                disabled={toDateDisabled ? 'disabled' : ''}
              />
            </div>
            <div className='form-group'>
              <textarea
                name='description'
                cols='30'
                rows='5'
                placeholder='Program Description'
                value={description}
                onChange={(e) => onChange(e)}
              />
            </div>
            <Link className='btn btn-light mg-1' to='/dashboard'>
              <i class='fas fa-arrow-left'></i> Go Back
            </Link>
            <input type='submit' className='btn btn-success mg-1' />
          </form>
        </div>
      </section>
    </Fragment>
  )
}

AddEducation.propTypes = {
  addEducation: PropTypes.func.isRequired,
}

export default connect(null, { addEducation })(withRouter(AddEducation))
